// базовые подключения
@import "vars";
@import "mixins";
@import "fonts";
@import "settings";

// подключения компонентов страницы
@import "./components/header";

// тестовые стили, удалите их
body{
  font-family: 'Inter', sans-serif;
}
:root{
  --white:#ffffff;
  --grey:#838383;
  --dgrey:#1d1d1f;
  --ofset:190px;
}
.hero{

  padding-top: 200px;
  height: 100vh;
  min-height: 700px;
  background-image: url('../img/Обложка.jpg');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
header{
  align-items: center;
position: absolute;
left: 0;top: 0;width: 100%;height: 120px;
}
.container{
  max-width: 1230px;
  padding: 0 15px;
  margin: 0 auto;
}
.header>.container{
  display: flex;
  flex-direction: row;
  justify-content: space-between;

}
.header__nav{
  display: flex;
  flex-direction: row;
}
.header__right{
  display: flex;
  align-items: center;
}
.nav__list{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 400px;
  height: 120px;
}
.nav__item:not(:last-child){
  margin-right: 35px;
}
.nav__link{
  font-weight: 400;
font-size: 16px;
line-height: 19px;
text-align: right;
color: var(--white);
}
.header__search{
  width: 24px;height: 24px;
  margin-right: 35px;
}
.hero__title{
  margin-top: 150px;
  margin-bottom: 30px;
  font-family: 'Inter';
font-style: normal;
font-weight: 600;
font-size: 96px;
line-height: 116px;
max-width: 936px;
height: 232px;
color: var(--white);
}
.hero__text{
  font-family: 'Inter';
font-style: normal;
font-weight: 400;
font-size: 16px;
line-height: 150%;
/* or 24px */
max-width: 468px;
height: 48px;

/* Fill/Color 1 – white */

color: #FFFFFF;
}
.btn{
  margin-top: 50px;
  background-color: var(--white) ;
  width: 180px;
height: 60px;
}
body > main > section.hero > div > div.hero__button > button{
  font-family: 'Inter';
font-style: normal;
font-weight: 600;
font-size: 17px;
line-height: 21px;
/* identical to box height */

text-align: center;
}
body > header > div{align-items: center;}
.about__list{
  display: grid;
  grid-template-columns: repeat(12,1fr);
  column-gap: 60px;
}
.about {
  padding: 190px 0;
}
.about__left {
}
.container {
}
.about__list {
}
.about__listleft {
  grid-column: 6 span;
}
.about__item:not(:last-child) {
  margin-bottom:60px;

}
.about__count {
  font-family: 'Inter';
font-style: normal;
font-weight: 300;
font-size: 64px;
line-height: 140%;
}
.about__counttext {
  font-family: 'Inter';
font-style: normal;
font-weight: 400;
font-size: 16px;
line-height: 150%;
color: var(--grey);
}
.about__right {
  grid-column: 5 span;

}
.about__h1 {font-family: 'Inter';
  font-style: normal;
  font-weight: 700;
  font-size: 44px;
  line-height: 140%;
