}
.video__container {

position: absolute;
}
body > main > section.video > div{
  height: 630px;
}
.video__video {
  height: 630px;
  width: 1600px;
}
.video__button {
  background-image: url("../img/svg/Play.svg");
  width: 100px;
height: 100px;
position: relative;
top: 50%;
left: 50%;
transform: translateY(-50%);
}
.portfolio {margin: 190px 0px;
}
.portfolio__list {
}
.portfolio-item {
}
.portfolio__col {
  grid-column: 6 span;
}
.portfolio__textitem {
  font-family: 'Inter';
font-style: normal;
font-weight: 400;
font-size: 16px;
line-height: 150%;
}
.portfolio__header {font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 140%;
}
.portfolio__text {
}
.portfolio__button {
  background-color: var(--dgrey);
