.quote>.container {

  margin-bottom: 190px;
  display: grid;
  grid-template-columns: repeat(12,1fr);
}
.quote__text {
  margin: 74px 0 30px 0;
  grid-column: 8 span;
  font-family: 'Inter';
font-style: normal;
font-weight: 500;
font-size: 36px;
line-height: 150%;
height: 378px;
color: var(--dgrey);
}
