}
.services {
}
.services__img {
}
.services__list {
}
.services__item {
  border-top: 1px solid  #73737e;
  height: 152px;
  align-items: center;
}
.services__name {
  grid-template-columns: 6 span;
  font-family: 'Inter';
font-style: normal;
font-weight: 500;
font-size: 32px;
line-height: 130%;
color: var(--dgrey);
}
.services__text {grid-template-columns: 6 span;  font-family: 'Inter';
font-style: normal;
font-weight: 400;
font-size: 16px;
line-height: 150%;
/* or 24px */


